.page {
  font-family: Arial, Helvetica, sans-serif;
  font-size: 100px;
  color: $white-color;

  &__container {
    display: grid;
    grid-template-columns: repeat(1, minmax($box-size, 1fr));

    @media (min-width: getSum($box-size, 2)) {
      grid-template-columns: repeat(2, minmax($box-size, 1fr));
    }
    
    @media (min-width: getSum($box-size, 3)) {
      grid-template-columns: repeat(3, minmax($box-size, 1fr));
    }
  }
}
